import { Component } from 'react';
import { connect } from 'react-redux';
import { Nav } from 'react-bootstrap';
import {
  HashRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import { type } from '../../store/type';
import { UiCarousel } from './carousel';
import Note from './note/index';
import Welcome from './welcome/index';

import './index.scss'

class Release extends Component {
  render() {
    return <div className="ui-release">
      <div className="sidemenu">
        <Nav activeKey="/#/release/screenshot" className="flex-column">
          <Nav.Link href='/#/release/screenshot'>Screenshots</Nav.Link>
          <Nav.Link href="/#/release/note">Log</Nav.Link>
        </Nav>
      </div>
      <div className="content">
        <Route path='/release' component={Welcome} />
        <Route path='/release/screenshot' component={UiCarousel} />
        <Route path='/release/note' component={Note} />
      </div>
    </div >;
  }
}

const mapStateToProps = (state, props) => {
  return {
    selected: state.timeline.selected
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    refreshThemes: () => { dispatch({ type: type.REFRESH_THEME }); }
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Release)
