import { useState } from 'react';
import { Carousel } from 'react-bootstrap';
import './index.scss'

export function UiCarousel() {
  const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex: any, e: any) => {
    setIndex(selectedIndex);
  };

  return (
    <Carousel className="ui-carousel" activeIndex={index} onSelect={handleSelect}>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src="https://img2.99diary.com/project/src/page-fleece-resource/20210329.png"
          alt="First slide"
        />
        <Carousel.Caption>
          <h3>First slide label</h3>
          <p>
            screenshot 20170809
          </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src="https://img2.99diary.com/project/src/page-fleece-resource/20180528.png"
          alt="Second slide"
        />

        <Carousel.Caption>
          <h3>Second slide label</h3>
          <p>
            screenshot 20180528
          </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src="https://img2.99diary.com/project/src/page-fleece-resource/20170809.png"
          alt="Third slide"
        />

        <Carousel.Caption>
          <h3>Third slide label</h3>
          <p>
            screenshot 20210329
          </p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}